.sponsor-button {
    background: #83c579;
    border: none;
    color: #fff;
    box-shadow: 0 6px #78ac5f;
    transition: none;
    border-radius: 20px;
	font-family: 'Noto Sans TC', sans-serif;
	font-size: 14px;
	cursor: pointer;
	padding: 15px 35px;
	display: inline-block;
	text-transform: uppercase;
	letter-spacing: 4px;
	outline: none;
	position: relative;
    border-radius: 20px;
    bottom: 6em;
    left: 1em;
}
.sponsor-button:after {
	content: '';
	position: absolute;
	z-index: -1;
	transition: all 0.3s;
}
.sponsor-button:hover {
	box-shadow: 0 4px #78ac5f;
	bottom: 5.8em;
}
.sponsor-button:active {
	box-shadow: 0 0 #78ac5f;
    bottom: 5.7em;
}
.sponsor-button:focus {
    outline: none;
}

@media (max-width: 767px){
    .sponsor-button {
        bottom: -1em;
    }
    .sponsor-button:hover {
        bottom: -1.2em;
    }
    .sponsor-button:active {
        box-shadow: 0 0 #78ac5f;
        bottom: -1.3em;
    }    
}
@media(max-width:768px){
    .sponsor-title{
        font-size: 2em;
    }
}

