.slides {
    position: relative;
}
  
.slides .slick-prev,
.slides .slick-next {
    position: absolute;
    top: 50%;
    z-index:1;
}
.slides .slick-prev {
    border-radius: 100%;
    background-color: #f5f5f5;
    border: 0;
    width: 40px;
    height: 40px;
    position: absolute;
    left: 28%;
    top: 85%;
    background-image: url(data:image/png;base64,);
    background-position: 50%;
}
.slides .slick-next {
    border-radius: 100%;
    background-color: #f5f5f5;
    border: 0;
    width: 40px;
    height: 40px;
    position: absolute;
    right: 28%;
    top: 85%;
    background-image: url(data:image/png;base64,);
    background-position: 50%;

}
.slick-prev:before, .slick-next:before{
    color: transparent !important;
}
.slick-prev:hover, .slick-prev:focus{
    background: #f5f5f5 url(data:image/png;base64,) 50% !important;
}
.slick-next:hover, .slick-next:focus {
    background: #f5f5f5 url(data:image/png;base64,) 50% !important;
}

@media (max-width:767px){
    .slides .slick-prev{
        left: 0;
        top:50%;
    }
    .slides .slick-next{
        right: 0;
        top:50%;
    }
    .teacher-description{
        font-size:3vw;
    }
}
