.page-enter {
  opacity: 0.01;
  -webkit-transform: scale(0.9) translateY(50%);
          transform: scale(0.9) translateY(50%);
}
.page-enter-active {
  opacity: 1;
  -webkit-transform: scale(1) translateY(0%);
          transform: scale(1) translateY(0%);
  transition: all 300ms ease-out;
}
.page-exit {
  opacity: 1;
  -webkit-transform: scale(1) translateY(0%);
          transform: scale(1) translateY(0%);
}
.page-exit-active {
  opacity: 0.01;
  -webkit-transform: scale(0.9) translateY(50%);
          transform: scale(0.9) translateY(50%);
  transition: all 300ms ease-out;
}
